.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background-color: #111;
}

.App > .container {
  flex: 1;
  display: flex;
  margin: 0 5vw;
  flex-direction: column;
}

.App-header {
  color: white;
}

.techs {
  margin: 8px;
  flex: 0;
  display: flex;
  flex-direction: row;
  gap: 8px;
}

.techs > img {
  height: 64px;
  pointer-events: none;
  background-color: #111;
  border: 2px solid #333;
  padding: 8px;
  border-radius: 8px;
}


@media (prefers-reduced-motion: no-preference) {

}

.App-header {
  padding: 8px;
  background-color: #222;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white;
  background-color: #111;
}

.App-link {
  color: #61dafb;
}

.App-bottom {
  flex: 1;
  background-color: #222;
  border-radius: 8px;
}